  width: 35vw;
  height: 35px;
}

#search-btn {
  height: 2remx;
  color: #fff;
  background-color: gray;
  border: none;
  border-radius: 5px;
  width: 4rem;
  margin-left: 10px;
  padding: 3px;
}

.res-container {
  flex-wrap: wrap;
  justify-content: center;
  margin: -10px;
  display: flex;
}

.res-card {
  border-radius: 13px;
  width: 250px;
  height: 280px;
  margin: 20px;
  transition: transform .2s ease-in-out;
}

.res-card:hover {
  transform: scale(1.05);
}

.res-card img {
  border-radius: 12px;
  width: 325px;
  height: 140px;
}

.res-card h3 {
  text-align: center;
